.detail .container{
  padding: 20px;
  max-width: 1300px;
  margin: auto;
}

.detail .image{
  padding: 0;
  margin: 0;
  height: 260px;
}

.image img{
  width: 100%;
  height: 100%;
  background-position: center;
  /* size: cover; */
  object-fit: cover;
  overflow: hidden;
  background-blend-mode: darken;
}


.detail .container .information {
  padding: 1rem;
  display: flex;
  flex-direction: column;
}

.detail .container .information .rating{
  display: flex;
  justify-content: left;
  align-items: left;
  margin: 5px 0;
}

.detail .container .information .moreInfo{
  display: flex;
  max-width: 1000px;
  padding: 5px;
}

.detail .container .information .moreInfo .name{

}

.detail .container .information .moreInfo .disc{
  width: 60%;
  margin-right: 50px;
  padding-right: 35px;
  border-right: 1px solid rgb(161, 161, 161);
}


.detail .container .menu{
  max-width: 1300px;
  margin: auto;
}

.detail .container .menu .menu_container{
  display: flex;
}

.detail .container .displayMenu{
  width: 70%;
  display: flex;
  flex-direction: column;
  grid-gap: 30px;
  gap: 30px;
  padding: 20px 40px;
}

.detail .container .menu_container .cartView{
  width: 30%;
}


@media(max-width: 700px){  
  .detail .container .displayMenu{
    display: flex;
    flex-direction: column;
  }

}


@media(max-width: 500px){ 
  .detail .container{
    padding: 20px;
    max-width: 1300px;
    margin: auto;
  }

  .detail .image{
    padding: 0;
    margin: 0;
    height: 160px;
  }

  .detail .container .information {
    padding: 0rem;
    display: flex;
    flex-direction: column;
  }

  .detail .container .information .rating{
    display: flex;
    justify-content: left;
    align-items: left;
    margin: 5px 0;
  }

  .detail .container .information .moreInfo{
    display: flex;
    flex-direction: column;
    max-width: 1000px;
    padding: 5px;
  }

  .detail .container .information .moreInfo .disc{
    width: 100%;
    margin-right: 50px;
    padding-right: 35px;
    margin-bottom: 15px;
    border-right: 0;
    border-bottom: 1px solid rgb(161, 161, 161);
  }

  .detail .container .menu .menu_container{
    display: block;
  }
  
  .detail .container .displayMenu{
    width: 100%;
    display: flex;
    flex-direction: column;
    grid-gap: 30px;
    gap: 30px;
    padding: 0px;
    /* border: 1px solid black; */
  }
  
  .detail .container .menu_container .cartView{
    width: 100%;
    /* display: none; */
  }

}
.foodCard{
  display: flex;
  justify-content: space-between;
}

.foodCard .name, .foodCard .price{
  display: flex;
  flex-direction: column;
}

.foodCard .infos{
  width: 30%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.foodCard .name{
  width: 70%;
}
.foodCard .name h3{
  font-size: 23px;
  margin-bottom: 10px;
}

.foodCard .name p{
  font-size: 14px;
}

.foodCard .price, .foodCard .add{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.foodCard .add .addIcon{
  border: 1px solid black;
  padding: 3px;
  border-radius: 15px;
}


@media(max-width: 500px){
  .foodCard .name h3{
    font-size: 20px;
    margin-bottom: 10px;
  }
  
  .foodCard .name p{
    font-size: 13px;
  }
  
  .foodCard .name{
    width: 80%;
  }

  .foodCard .infos{
    width: 20%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
  }

  .foodCard .price p{
    margin: 0;
  }
  .foodCard .price{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}

.cart{
  width: 100%;
  padding: 24px 10px;
}
.cart h1{
  text-align: center;
  padding-bottom: 3px;
  font-weight: bold;
}

.cart .head{
  display: flex;
  justify-content: center;
  border-bottom: 3px solid rgb(161, 161, 161);
}

.cart .product{
  display: flex;
  justify-content: space-between;
  padding: 10px 0;
  border-bottom: 1px solid rgb(219, 219, 219);
}

.cart .product .name{
  width: 50%;
}
.cart .product .price{
  font-weight: bold;
  width: 20%;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* .cart .product .action{
  width: 10%;
  display: flex;
  justify-content: center;
  align-items: center;
} */

.cart .product .qty{
  width: 30%;
  display: flex;
  grid-gap: 5px;
  gap: 5px;
  /* justify-content: center; */
  align-items: center;
}
.cart .product .qty .number{
  align-items: center;
  text-align: center;
  display: flex;
  justify-content: center;
  vertical-align: center;
}

.cart .product .qty .icons{
  display: flex;
  justify-content: center;
}

.cart .total{
  align-items: right;
  text-align: right;
}

.cart .checkout{
  display: block;
}
.cart .checkout button{
  width: 100%;
  padding: 5px 10px;
  margin: 10px 0;
  background-color: #FFE45E;
}



.bold{
  font-weight: bold;
