.cartItemView{
    padding: 0;
    margin: 0;
    width: 100%;
}


.cartItem{
    width: 100%;
    padding: 0;
    margin: 0;
    display: flex;
    height: 100%;
    align-items: center;
    justify-content: space-around;
}

.cartItemHolder{
    width: 100%;
    height: 100%;
    padding: 4px;
    margin: 5px 10px;
    margin-bottom: 8px;
    display: grid;
    grid-template-columns: 2fr 2fr 2fr 1fr;
    border-radius: 2px;
    place-items: center;
    align-items: center;
    justify-content: center;
    border-bottom: 1px solid gray;
}

a{
    text-decoration: none;
    cursor: pointer;
}

/* .cartItem_img{
    width: 50px;
    height: 50px;
    background: transparent;
    background-color: transparent;
    object-fit: contain;
} */

/* .cartItem_img img{
    height: fill;
    width: fill;
    object-fit: contain;
    background-color: transparent;
    background: transparent;
    scale: 1;
} */
/* .cartItem_brand{
    font-size: 12px;
    color: black;
    font-weight: lighter;
} */
.cartItem_name{
    text-decoration: none;
    color: black;
    font-size: 12px;
}


.cartItem .qty{
    width: 30%;
    display: flex;
    grid-gap: 5px;
    gap: 5px;
    justify-content: center;
    align-items: center;
  }

.cartItem .qty .icons{
    display: flex;
    justify-content: center;
}

.cartItem_name:hover{
    color: rgb(33, 161, 247);
}
.cartItem_price{
    font-size: 12px;
    color: rgb(11, 190, 26);
    font-weight: normal;
}
.cartItem_select{
    padding: 4px 6px;
    cursor: pointer;
    border-radius: 3px;
    background: transparent;
    border:none;
    outline: none;
    -webkit-transform: 0;
            transform: 0;
    font-size: 12px;
    


}
.cartItem_select:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    transition: all ease 0.4s;
    background-color: white;
}
.cartItem_delete_btn{
    padding: 10px 17px;
    color: red;
    background-color: transparent;
    border: none;
    cursor: pointer;
    transition: all 0.4s ease;
}
.cartItem_delete_btn svg{
    font-size: 18px;
}
.cartItem_delete_btn:hover, 
.cartItem_delete_btn:active
.cartItem_delete_btn:focus
{
    color: rgb(253, 86, 86);
    font-weight: bolder;
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    
}


/* media query for responsiveness */

@media(max-width:910px){

    .cartItemHolder{
       
        padding: 2px;
        margin: 4px 10px;
        
    }


    .cartItem_img{
        width: 50px;
        height: 50px;
    }

        
    .cartItem_brand{
        font-size: 10px;
        

    }
    .cartItem_name{
       font-size: 10px;
    }

   
    .cartItem_price{
        font-size: 10px;
      
    }
    .cartItem_select{
        font-size: 10px;
    }
    
    .cartItem_delete_btn svg{
        font-size: 18px;
    }



}


@media(max-width:560px){
    
    .cartItem{
        width: 100%;
        padding: 0;
        margin: 0;
        height: 100%;
        /* align-items: center; */
        display: flex;
        justify-content: space-between;
    }
    .cartItemHolder{
        padding: 1px;
        margin: 4px 10px;
    }

    .cartItem_name{
        align-items: left;
       text-align: left;
       font-size: 12px;
    }
   
    .cartItem_price{
        font-size: 12px;
    }
    .cartItem_select{
        font-size: 10px;
    }
    
    .cartItem_delete_btn svg{
        font-size: 16px;
    }

    .stepOneContainer{
        margin: 0;
    }

}

@media(max-width: 370px){
    .cartItemHolder{
        padding: 1px;
        margin: 3px 8px;
    }

    .cartItem_name{
       font-size: 12px;
    }

    .cartItem_price{
        font-size: 10px;
    }
    .cartItem_select{
        font-size: 8px;
    }
    
    .cartItem_delete_btn svg{
        font-size: 14px;
    }
    
}

