@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,300;0,500;0,700;0,800;1,100;1,300;1,500;1,700&family=Roboto:ital,wght@0,100;0,700;1,100;1,300;1,400;1,500&display=swap");

$Poppins: "Poppins", sans-serif;

//colors
$dark: #1d3124;
$light: hsl(51, 44%, 94%);
$primary: rgb(202, 140, 7);
$main: rgb(255 219 0);
$bg: rgb(244, 244, 255);

//spacing

$spacing-md: 12px;
$spacing-lg: 32px;

//fonts
$sm-tl: 25px;
$md-tl: 30px;
$lg-tl: 35px;

//border radius

$borderRadius: 12px;

//header height
$headerHeight: 50px;

//restaurant card width
$restCardWidth: 345px;

//mix

@mixin breakpoint($point) {
  @if $point == sm {
    //480px
    @media (min-width: 480px) {
      @content;
    }
  }

  @if $point == md {
    // 768px
    @media (min-width: 780px) {
      @content;
    }
  }
  @if $point == mdP {
    @media (min-width: 920px) {
      @content;
    }
  }
  @if $point == lg {
    // 1080px
    @media (min-width: 1080px) {
      @content;
    }
  }
}
