/* helllo friend */
@import "../../styles/variables.scss";

@import url("https://fonts.googleapis.com/css?family=Montserrat:400,800");

* {
  box-sizing: border-box;
}

.login {
  color: rgb(186, 186, 186);
}

.login .erroMessage {
  padding: 3px 0;
  margin: 0;
  color: red;
}

.login .login_offscreen,
.login .signUpoffscreen {
  position: absolute;
  left: -9999px;
}

.login .main {
  background: #f3f08e;
  /* background: #f6f5f7; */
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-family: sans-serif;
  height: 100vh;
  margin: 0;
  /* margin: -20px 0 20px; */
}

.login h1 {
  font-weight: bold;
  margin: 0;
}

.login h2 {
  text-align: center;
}

.login p {
  font-size: 14px;
  font-weight: 100;
  line-height: 20px;
  letter-spacing: 0.5px;
  margin: 10px 0 5px;
}

.login span {
  font-size: 12px;
}

.login a {
  color: #333;
  font-size: 14px;
  text-decoration: none;
  margin: 15px 0;
}

.login button {
  border-radius: 20px;
  border: 1px solid #ff4b2b;
  background-color: #ff4b2b;
  color: #000000;
  /* color: #FFFFFF; */
  font-size: 12px;
  font-weight: bold;
  padding: 12px 45px;
  letter-spacing: 1px;
  text-transform: uppercase;
  transition: transform 80ms ease-in;
}

.login button:active {
  transform: scale(0.95);
}

.login button:focus {
  outline: none;
}

.login button.ghost {
  background-color: transparent;
  border-color: #000000;
}

.login form {
  background-color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 50px;
  height: 100%;
  text-align: center;
}

.login input {
  background-color: #eee;
  border: none;
  padding: 12px 15px;
  margin: 8px 0;
  width: 100%;
}

.login .signinTitle {
  padding-bottom: 2rem;
}

.login .title {
  padding-bottom: 1rem;
}

.login .btnLink {
  color: #ff4b2b;
  cursor: pointer;
}

.login .container {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  position: relative;
  overflow: hidden;
  width: 768px;
  max-width: 100%;
  min-height: 620px;
}

.login .form-container {
  position: absolute;
  top: 0;
  height: 100%;
  /* transition: all 0.6s ease-in-out; */
}

.login .sign-in-container {
  left: 0;
  width: 50%;
  z-index: 2;
}

.login .container.right-panel-active .sign-in-container {
  transform: translateX(100%);
}

.login .sign-up-container {
  left: 0;
  width: 50%;
  opacity: 0;
  z-index: 1;
}

.login .container.right-panel-active .sign-up-container {
  transform: translateX(100%);
  opacity: 1;
  z-index: 5;
  animation: show 0.6s;
}

@keyframes show {
  0%,
  49.99% {
    opacity: 0;
    z-index: 1;
    transition: all ease 0.3s;
  }

  50%,
  100% {
    opacity: 1;
    z-index: 5;
    transition: all ease 0.3s;
  }
}

.login .overlay-container {
  position: absolute;
  top: 0;
  left: 50%;
  width: 100%;
  height: 100%;
  overflow: hidden;
  transition: transform 0.6s ease-in-out;
  z-index: 100;
  // background-color: rgb(186, 186, 186);
  background-color: #000000;
}

.login .container.right-panel-active .overlay-container {
  transform: translateX(-100%);
}

.login .overlay {
  background: #f3f08e;
  /* background: -webkit-linear-gradient(to right, #FF4B2B, #FF416C); */
  /* background: linear-gradient(to right, #FF4B2B, #FF416C); */

  /* background: #FF416C;
	background: -webkit-linear-gradient(to right, #FF4B2B, #FF416C);
	background: linear-gradient(to right, #FF4B2B, #FF416C); */
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 0 0;
  color: #000000;
  position: relative;
  left: -50%;
  height: 100%;
  width: 100%;
  transform: translateX(50%);
  transition: transform 0.6s ease-in-out;
}

.login .container.right-panel-active .overlay {
  transform: translateX(50%);
}

.login .overlay-panel {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 40px;
  text-align: center;
  top: 0;
  height: 100%;
  width: 50%;
  transform: translateX(0);
  transition: transform 0.6s ease-in-out;
}

.login .overlay-left {
  transform: translateX(0%);
  width: 50%;
}

.login .container.right-panel-active .overlay-left {
  transform: translateX(0);
}

.login .overlay-right {
  /* display: none; */
  right: 0;
  transform: translateX(0);
}

.login .container.right-panel-active .overlay-right {
  transform: translateX(0%);
}

.login .social-container {
  margin: 20px 0;
}

.login .social-container a {
  border: 1px solid #dddddd;
  border-radius: 50%;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin: 0 5px;
  height: 40px;
  width: 40px;
}

@media (max-width: 580px) {
  .login .overlay-container {
    display: none;
  }

  .login .sign-in-container {
    left: 0;
    width: 100%;
    z-index: 2;
  }

  .login .container.right-panel-active .sign-in-container {
    transform: translateX(100%);
  }

  .login .container.right-panel-active .sign-up-container {
    left: -100%;
    width: 100%;
    opacity: 0;
    z-index: 1;
  }

  .login .container.right-panel-active .sign-up-container {
    transform: translateX(100%);
    opacity: 1;
    z-index: 5;
    animation: show 0.6s;
  }
}
